.after-none {
  &:after {
    opacity: 0;
    display: none;
  }
}
section.build-main.core-solution-wrap.c-f-t-a-wrap {
  @include rem(padding-top, 0);
}
.meet-the-team-profile-wrap {
  @include rem(padding-bottom, 60px);
  @include media-breakpoint-down(sm) {
    @include rem(padding-top, 120px);
    height: 540px;
    .case-studies-right {
      bottom: -120px;
      height: 690px;
    }
  }
  p {
    color: $white;
  }
  .shelbe-marketer-wrap {
    align-items: baseline;
    @include rem(margin-bottom, 5px);
    @include media-breakpoint-down(sm) {
      position: relative;
      bottom: inherit;
    }
    .shelbe-text {
      color: $white;
      text-align: left;
      @include media-breakpoint-down(sm) {
        @include rem(font-size, 34px);
      }
    }
    p {
      color: $white;
    }
  }
}

@include media-breakpoint-down(sm) {
  section.case-studies-wrap.meet-the-team-wrap {
    .meet-the-team-profile-wrap {
      height: auto;
    }
  }
}

section.case-studies-wrap.meet-the-team-wrap {
  .case-studies-inner {
    @include rem(margin-top, -50px);
    h2 {
      &:before {
        opacity: 0;
        display: none;
      }
    }
  }
}
.sm-title-bg {
  &:before {
    position: absolute;
    background-image: url(/wp-content/uploads/2021/03/yellow-marker-small.svg);
    background-repeat: no-repeat;
    content: "";
    width: 100%;
    height: 90px;
    left: -35px;
    z-index: 0;
    top: 10px;
  }
  position: relative;
  h2 {
    z-index: 9;
    position: relative;
  }
}
.profile-ceo-wrap {
  width: 160px;
  height: 160px;
  border-radius: 100%;
  overflow: hidden;
  border: solid 3px $white;
  @include rem(margin-top, -50px);
  @include rem(margin-bottom, 20px);
  @include media-breakpoint-down(sm) {
    width: 120px;
    height: 120px;
  }
  img {
    border-radius: 100%;
  }
}
.list-star-link {
  @include media-breakpoint-down(md) {
    @include rem(margin, 0);
  }
  li {
    position: relative;
    @include rem(margin-left, 17px);
    &:before {
      content: "";
      background-image: url(/wp-content/uploads/2021/04/plus-single-orange.svg);
      width: 12px;
      height: 12px;
      background-size: cover;
      position: absolute;
      left: -17px;
      top: 10px;
    }
    a {
      color: $dark-blue;
      text-decoration: none;
      display: inline-block;
      @include rem(margin-bottom, 10px);
      line-height: 28px;
      border-bottom: solid 3px transparent;
      &:hover {
        font-weight: bold;
        border-bottom: solid 3px $bright-blue;
      }
    }
  }
}

section.case-studies-wrap.meet-the-team-wrap {
  .meet-the-team-profile-wrap {
    .case-studies-right {
      background: $dark-blue;
    }
  }
}

@include media-breakpoint-down(sm) {
  .page-template-about-page{
    section.case-studies-wrap.meet-the-team-wrap {
      @include rem(margin-top, 40px);
      .case-studies-right {
        top: 210px;
        height: 80%;
      }
      ul.slick-dots {
        list-style: none;
        top: 47px;
        li {
          width: 15px;
          &:before {
            display: none;
          }
          >button {
            font-size: 0;
            border: 0;
            background-color: $bright-orange;
            width: 12px;
            height: 12px;
            @include rem(margin, 0);
            border-radius: 20px;
            transition: all 0.3s;
            outline: none;
            display: inline-block;
            min-width: 12px;
            min-height: 12px;
            &:before {
              display: none;
            }
          }
        }
        li.slick-active {
          >button {
            width: 16px;
            height: 16px;
            min-width: 16px;
            min-height: 16px;
          }
        }
      }
    }
  }
  .slick-slider {
    ul.slick-dots {
      li.slick-active {
        button {
          background-color: #ff850d !important;
        }
      }
      li {
        button {
          background-color: #C5C5C5 !important;
        }
      }
    }
  }
}